@import './mixins';
@import './variables';

body,
html {
    padding: 0;
    margin: 0;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

html {
    position: relative;
    min-height: 100%;
}

body {
    margin-bottom: 48px;
    overflow: hidden;
}

.header {
    width: 100%;
    max-height: 20%;
    box-shadow: 0px 5px 5px black;
    background:rgba(2, 0, 0, 0.836);
    transition: background 0.5s ease;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 1.2rem;
    color: white;
    padding: 1em;
    font-size: 3rem;
    .make-bigger {
        scale: 2.1;
        padding-right: 16px;
        font-weight: 600;   
        margin-left: 50px;
    }
}

.footer {
    width: 100%;
    background:rgba(2, 0, 0, 0.9);
    transition: background 0.5s ease;
    font-size: 1rem;
    line-height: 1;
    @include flex-center;
    overflow: hidden;
    color: $white;
    height: 5vh;
    position: absolute;
    bottom: 0;
}


.active {
    background-color: adjust-hue($color: $light-grey, $degrees: 10);
    font-weight: 600;
}

.main-section-container {
    display: flex;

    .box-container {
        width: 100%;
        @include flex-center-horizontal;
        margin: 2rem;
    }
    
    .list-container {
        width: 900px;
        // border: 2px solid blue;
        margin: 0 auto;
        padding: 10px;
        button {
            width: 100%;
            border: none;
            border-radius: 4px;
            text-align: left;
            padding: 0.25rem;
            @include flex-center-vertical;
        }
    }

    .sidebar {
        background-color: $light-grey;
        // max-width: 350px;
        width: 350px;
        padding: 10px;
        z-index: 5;
        height: 79vh;
        // margin: 10px;
        .project,
        .scheduled-item {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-radius: 12px;
            margin: 5px;
            p {
                font-size: medium;
                padding-left: 0.9rem;
            }
            &:hover {
                cursor: pointer;
                background-color: adjust-hue($color: $light-grey, $degrees: 200);
            }
        }
        .scheduled-container {
            margin-top: 20px;
            .scheduled-item {
                margin: 0.2rem;
                padding-left: 1rem;
            }
        }
        .project-container {
            .project-heading {
                margin-bottom: 5px;
                margin-left: 20px;
                font-weight: 600;
                font-size: 1.75rem;
            }
            .project {
                margin: 0.5rem;
                // padding: 0.05rem;
                padding-left: 1rem;
            }
        }
    }
}
